import React from 'react'
import { StyleSheet, View } from 'react-native'
import CocktailList from './CocktailList'
import Search from './Search'
import { data } from './fixtures'

const Discover = ({ navigation }) => {
  const [value, onChangeText] = React.useState('')
  const [results, setResults] = React.useState([])

  const handleSearchInputUpdate = (text) => {
    onChangeText(text)
    if (text.length >= 3) {
      setResults(data.filter((item) => {
        return item.key.toLowerCase().indexOf(text.toLowerCase()) !== -1
      }))
    } else {
      setResults([])
    }
  }

  const getDataToDisplay = () => {
    if (results.length === 0) return data
    console.log(results)
    return results
  }

  return (
    <View styles={styles.container}>
      <Search handleSearchInputUpdate={handleSearchInputUpdate} value={value} />
      <CocktailList data={getDataToDisplay()} navigation={navigation} />
    </View>
  )
}

const styles = StyleSheet.create({
  container: {
    backgroundColor: '#fff',
    alignItems: 'center',
    justifyContent: 'center'
  }
})

export default Discover
