import React from 'react'
import { StyleSheet, View, Text, TouchableWithoutFeedback } from 'react-native'

const CocktailListItem = (props) => {
  const handleTouch = () => {
    props.navigation.navigate('CocktailPage', { name: props.name })
  }

  return (
    <TouchableWithoutFeedback onPress={handleTouch}>
      <View
        style={{
          borderBottomColor: 'black',
          borderBottomWidth: 1
        }}
      >
        <View
          style={{
            paddingLeft: 120,
            paddingTop: 30,
            paddingBottom: 30
          }}
        >
          <Text style={styles.itemName}>{props.name}</Text>
          <Text style={styles.ingredients}>
            {props.primaryIngredient}, {props.secondaryIngredient}
          </Text>
          <Text style={styles.flavourProfile}>
            {props.flavourProfile}
          </Text>
        </View>
      </View>
    </TouchableWithoutFeedback>
  )
}

const styles = StyleSheet.create({
  itemDescription: {
    paddingLeft: 100
  },
  itemName: {
    fontSize: 20,
    fontWeight: 'bold'
  },
  ingredients: {
    fontSize: 14,
    fontStyle: 'italic'
  },
  flavourProfile: {
    fontSize: 16
  }
})

export default CocktailListItem
